import Vue from 'vue'
import VueRouter from 'vue-router'
import Login from '../views/Login'
import Home from '../views/Home'
import Chat from '../views/chat/FriendChat';

Vue.use(VueRouter)
/**
 * url`/test1``/test2`
 * Home.vue`<router-view/>`
 * App.vue`<router-view/>`
 */
const routes = [
    {
        path: '/',
        name: 'Login',
        component: Login,
    }, {
        path: '/home',
        name: 'Home',
        component: Home,
        children:[
            {
                path: "/chat",
                name: "",
                component: Chat
            }
        ]
    }
]

const router = new VueRouter({
    routes
})

export default router




