<template>
    <div>
        <el-container>
            <el-header class="header" height="80px">
                <div class="title"></div>
                <div>
                    <div class="chat-message">

                        <el-dropdown class="userInfo" @command="handleCommand">
                            <span class="el-dropdown-link" >
                                {{user.object.name}}<i><img :src="user.object.userface" alt=""></i>
                             </span>
                            <el-dropdown-menu slot="dropdown">
                                <el-dropdown-item command="userinfo"></el-dropdown-item>
                                <el-dropdown-item command="chatinfo"></el-dropdown-item>
                                <el-dropdown-item command="setting"></el-dropdown-item>
                                <el-dropdown-item command="logout" divided ></el-dropdown-item>
                            </el-dropdown-menu>
                        </el-dropdown>
                    </div>
                </div>

            </el-header>

            <el-container>
                <el-aside width="200px">
                    <!--`router`  index  path -->
                    <el-menu router>
                        <!--this.$router.options.routers,router/index.js-->
                        <el-submenu :index="index+''" v-for="(item,index) in routes" :key="index">
                            <template slot="title">
                                <i :class="item.iconcls" style="margin-right: 8px;color:dodgerblue;"></i>
                                <span>{{item.name}}</span>
                            </template>
                            <el-menu-item-group>
                                <el-menu-item :index="child.path" v-for="(child,indexj) in item.children" :key="indexj">{{child.name}}</el-menu-item>
                            </el-menu-item-group>

                        </el-submenu>

                    </el-menu>
                </el-aside>
                <el-main>
                    <el-breadcrumb separator-class="el-icon-arrow-right" v-show="this.$router.currentRoute.path!='/home'">
                        <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
                        <!---->
                        <el-breadcrumb-item>{{this.$router.currentRoute.name}}</el-breadcrumb-item>

                    </el-breadcrumb>
                    <div class="welcomevHr" @click="download" v-show="this.$router.currentRoute.path=='/home'" >
                        ()()
                    </div>

                    <!---->
                    <router-view/>
                </el-main>
            </el-container>
        </el-container>
    </div>
</template>

<script>
    /**
     *
     * vuenew vue({})export default {} 
     *
     * 1.new vue({})  main.js
     *   export default{}new Vue({})
     *
     * 2.export default{}class
     *
     * 3.datanew Vue
     *   export defaultdata
     *
     *
     *
     * &&
     * export default  Object components 
     *  Vue.extend  Object  Vue 
     *  Vue.extend new Vue
     *  Vue 
     *
     *
     * 
     *  data 
     *  data  object
     */
    export default {
        name: "Home",
        data(){
            return {
                user:JSON.parse(window.sessionStorage.getItem("user"))
            }
        },
        methods:{
            handleCommand:function (command) {
                /**
                 * 
                 */
                if (command === 'logout') {
                    this.$confirm('?', '', {
                        confirmButtonText: '',
                        cancelButtonText: '',
                        type: 'warning'
                    }).then(() => {
                        this.getRequest('/logout');
                        //
                        window.sessionStorage.removeItem("user");
                        //Vuex
                        this.$store.commit("initRoutes", []);
                        this.$router.replace("/")
                    }).catch(() => {
                        this.$message({
                            type: 'info',
                            message: ''
                        });
                    });
                }else if (command === 'chatinfo') {
                    this.$router.push("/chat");
                }
            },
            download() {
                window.open("/salary/sob/download", "_parent");
            }
        },
        computed:{
            /**
             *  hidden  false routes
             * @returns {RouteConfig[]}
             */
            activeRoutes:function () {
                return this.$router.options.routes;
            },
            routes:function () {
                //$store  Vue Vue.prototypeVue
                return this.$store.state.routes;
            }
        },
        mounted() {
            //
            this.$store.dispatch('connect');

        }
    }
</script>

<style scoped>
    .header {
        background-color: #409EFF;
        color: white;
        display: flex;
        align-items: center;
        justify-content: space-between;

    }
    .title {
        font-size: 30px;
        font-family: ;
    }

    .userInfo{
        color: white;
        cursor: pointer;
    }

    .userInfo img {
        margin-left: 8px;
        width: 48px;
        height: 48px;
        border-radius: 24px;
    }
    .el-dropdown-link{

        display: flex;
        align-items: center;
        font-size: 14px;
    }
    .welcomevHr {
        cursor: pointer;
        font-size: 30px;
        text-align: center;
        padding-top:100px;
        color: dodgerblue;

    }

</style>