/**
 * axios
 */

import axios from 'axios';
import {Message} from "element-ui";
import Qs from 'qs'
import router from "../router";
/* :thencatch*/
/**
 * response
 * http
 * http200
 *
 *  error
 * httphttp
 * http
 */

axios.interceptors.response.use(response => {

    console.log(response)
    //success.status 
    //success.status
    //success.data.status
    if (response.status && response.status == 200 && response.data.status == 500) {
        //
        Message.error({
            message:response.data.msg,
        })
        /*
        *  Message(options) type 
        *  Message.success(options) Message.closeAll() 
        * */
        return;
    }
    if (response.status && response.status == 200 && response.data.status == 200) {
        //
        //
        if (response.data.msg != null) {
            Message.success({
                message: response.data.msg,
            });
        }
        return response.data;
    }
}, error => {
    console.log("error:"+error)
    if (error.response.status == 503 || error.response.status == 404) {
        Message.error({
            message: "(  )",
        })
    }else if (error.response.status == 403) {
        Message.error({
            message: "(,, . ,,)QQ:1639670695!",
        })
    }else if (error.response.status == 401) {
        Message.error({
            message: ".",
        })
        router.replace("/");
     //else 
    }else {
        //http
        if (error.response.data.msg) {
            Message.error({
                message:error.response.data.msg,
            })
        }else{
            Message.error({
                message: ".",
            })
        }
    }
    return;
});

/**
 *  
 */
//
let base = '';
/**
 * key-value
 *  json
 * export default 
 * export const 
 */
export const  postKeyValueRequest=(url,params)=>{
    //axiosaxiosAjax
    return axios({
        method: "post",
        //jQuery1
        url: `${base}${url}`,
        data: params,
        // `transformRequest` 
        //  'PUT', 'POST'  'PATCH' 
        //  ArrayBuffer Stream
        transformRequest:[function (data) {
            //data`key=value&key=value`
            data = Qs.stringify(data)
            //console.log(data);
            return data;
        }],
        // `headers` 
        headers:{
            //
            'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'
        }
    });
}
/**
 * post
 * @param url
 * @param params
 * @returns {AxiosPromise}
 */
export const postRequest = (url, params) => {
    return axios({
        method: "post",
        url: `${base}${url}`,
        data: params
    })
};

/**
 * get
 * @param url
 * @param params
 * @returns {AxiosPromise}
 */
export const getRequest = (url, params) => {
    return axios({
        method: "get",
        url: `${base}${url}`,
        data: params
    })
};

/**
 * put
 * @param url
 * @param params
 * @returns {AxiosPromise}
 */
export const putRequest = (url, params) => {
    return axios({
        method: "put",
        url: `${base}${url}`,
        data: params
    })
};


/**
 * delete
 * @param url
 * @param params
 * @returns {AxiosPromise}
 */
export const deleteRequest = (url, params) => {
    return axios({
        method: "delete",
        url: `${base}${url}`,
        data: params
    })
};



